<template>
  <div class="news">
    <van-nav-bar title="专栏详情" left-arrow @click-left="onClickLeft" size="0.2rem" />
    <h3>{{active.title}}</h3>
    <img :src="active.pic" alt />
    <div class="con" v-html="active.content"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: {}
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    }
  },
  created() {
    //   console.log("get传值",this.$route.query)
    //   console.log("动态传值",this.$route.params)
    this.http.get("cms/news/detail?id=" + this.$route.query.id).then(msg => {
      console.log(msg);
      this.active = msg.data.data;
    });
  }
};
</script>

<style lang="scss" >
.news {
  width: 5.2rem;
  h3 {
    font-size: 0.3rem;
    color: #333333;
    width: 100%;
    text-align: center;
    margin-top: 0.45rem;
    margin-bottom: 0.2rem;
  }
  img {
    width: 4.85rem;
    height: 3.13rem;
    display: block;
    margin: 0 auto;
  }

  .con {
   
      padding: 0 0.2rem;
      box-sizing: border-box;
      font-size: 0.2rem;
      color: #333333;
      line-height: 0.48rem;
      text-indent: 2em;
      margin-top: 0.1rem;
  }
}
</style>


