<template>
  <div>
    <van-nav-bar title="确认订单" left-arrow @click-left="onClickLeft" />
    <div class="address" @click="goAddresslist">
      <p>
        <span>{{address.linkMan}}</span>
        <span>{{address.mobile}}</span>
      </p>
      <p>{{address.address}}</p>
    </div>
    <p class="mean">商品列表</p>
    <ul>
      <li v-for="item of $store.getters.order" :key="item.id">
        <img :src="item.pic" alt />
        <div>
          <p>{{item.name}}</p>
          <div class="pff">
            <p v-for="p of item.pro" :key="p.id">{{p.name}}</p>
          </div>
        </div>
        <div>
          <p>￥{{item.price}}</p>
          <p>x{{item.num}}</p>
        </div>
      </li>
    </ul>
    <div class="pei">
      <p>配送方式</p>
      <p>快递</p>
    </div>
    <div class="bei">
      <label for="cont">备注</label>
      <input type="text" placeholder="如需备注请输入" class="inps" />
      <p class="pss"></p>
    </div>
    <div class="jin">
      <p>商品金额</p>
      <p>￥{{$store.getters.hj}}</p>
    </div>
    <div class="foots">
      <p>合计：￥{{$store.getters.hj}}</p>
      <p @click="gopay">提交订单</p>
    </div>
  </div>
</template>

<script>
import qs from "qs";
export default {
  data() {
    return {
      address: {}
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    goAddresslist() {
      this.$router.push("/addresslist");
    },
    gopay() {
      this.$router.push("/pay");
      //   var re = this.$store.getters.order.map(item => {
      //     var obj = {};
      //     var str = [];
      //     obj.goodsId = item.id;
      //     obj.number = item.num;
      //     item.pro.forEach(element => {
      //       str.push(element.propertyId + ":" + element.id);
      //     });

      //     obj.propertyChildIds = str.join(",");
      //     return obj;
      //   });
      //   console.log(re);
      //   var token = localStorage.getItem("token");
      //   if (token != null) {
      //     this.$router.push("/pay");
      //   }else{
      //     this.$router.push("/login");
      //   }
      //   re = JSON.stringify(re);
      //   var d = {
      //     token: token,
      //     goodsJsonStr: re
      //   };
      //   var d1 = qs.stringify(d);
      //   this.http
      //     .post("https://api.it120.cc/small4/order/create", d1)
      //     .then(msg => {
      //       console.log(msg);
      //       if (msg.data.msg == "success") {
      //         // console.log(234)
      //         // this.$router.push({name:"pay",params:{ordernum:msg.data.data.orderNumber,price:msg.data.data.amount}})
      //       }
      //     });
    }
  },
  created() {
    var data = { token: localStorage.getItem("token") };
    this.http
      .get(
        "https://api.it120.cc/small4/user/shipping-address/default?token=" +
          localStorage.getItem("token")
      )
      .then(msg => {
        console.log(msg);
        if (msg.data.msg == "success") {
          this.address = msg.data.data;
        }
      });
  }
};
</script>

<style lang="scss" scoped>
.address {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  align-items: center;
  height: 1.1rem;
  padding: 0 0.2rem;
  flex-wrap: wrap;
  p {
    width: 100%;
  }
}
.mean {
  height: 0.65rem;
  padding: 0 0.2rem;
  line-height: 0.65rem;
  font-size: 0.16rem;
  border-top: 0.13rem solid #f5f5f5;
  border-bottom: 2px solid #e8e8e8;
}
ul {
  border-bottom: 0.15rem solid #f5f5f5;
  li {
    height: 1.53rem;
    border-bottom: 1px solid #e8e8e8;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    img {
      width: 1.12rem;
      height: 1.12rem;
      display: block;
      margin-left: 0.2rem;
      background: red;
    }
    div:nth-of-type(1) {
      margin-left: 0.12rem;
      width: 3.06rem;
      height: 1.12rem;
      margin-top: 0.1rem;
      p:nth-of-type(1) {
        font-size: 0.18rem;
      }
      .pff {
        // width: 2.2rem;
        margin-top: 0.55rem;
        display: flex;
        margin-left: -0rem;

        p {
          font-size: 0.18rem;
          //   display: block;
          color: #999;
          margin-right: 0.1rem;
        }
      }
    }
    div:nth-of-type(2) {
      height: 1.34rem;
      p:nth-of-type(1) {
        font-size: 0.18rem;
        margin-top: 0.1rem;
      }
      p:nth-of-type(2) {
        font-size: 0.18rem;
        margin-top: 0.6rem;
        margin-left: 0.29rem;
        color: #999;
      }
    }
  }
}
.pei {
  height: 0.6rem;
  padding: 0 0.2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  font-size: 0.18rem;
  border-bottom: 0.01rem solid #e5e5e5;
}
.bei {
  height: 0.59rem;
  padding: 0 0.2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  label {
    cursor: default;
  }
  .inps {
    width: 3.1rem;
    height: 0.2rem;
    font-size: 0.18rem;
    color: #333;
    margin-left: 0.76rem;
    border: none;
  }
  .pss {
    width: 0%;
    height: 0.01rem;
    background: #0098e9;
    -webkit-transition: all 1s;
    transition: all 1s;
    margin: 0 auto;
  }
}
.jin {
  height: 0.64rem;
  padding: 0 0.2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  border-top: 0.15rem solid #f5f5f5;
  border-bottom: 0.15rem solid #f5f5f5;
}
.foots {
  width: 100%;
  background: white;
  height: 0.69rem;
  position: fixed;
  bottom: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  p:nth-of-type(1) {
    font-size: 0.2rem;
    color: #d00000;
    line-height: 0.69rem;
    margin-left: 2rem;
  }
  p:nth-of-type(2) {
    width: 1.73rem;
    height: 0.69rem;
    background: #b7282e;
    color: white;
    font-size: 0.22rem;
    text-align: center;
    line-height: 0.69rem;
  }
}
</style>


