<template>
  <div>
    <van-nav-bar title="新增地址" left-arrow @click-left="onClickLeft" />
    <ul>
      <li>
        <div>
          <p>联系人</p>
          <input type="text" placeholder="姓名" v-model="linkMan" />
        </div>
      </li>
      <li>
        <div>
          <p>手机号码</p>
          <input type="text" placeholder="11位手机号" v-model="mobile" />
        </div>
      </li>

      <li>
        <div @click="ads">
          <p>选择地区</p>
          <!-- <input type="text" placeholder  />
          -->
          <div v-if="area.length>0">
            <span class="di-c">{{ area[0].name }}</span>
            <span class="di-c">{{ area[1].name }}</span>
            <span class="di-c">{{ area[2].name }}</span>
          </div>
        </div>
      </li>
      <li>
        <div>
          <p>联系人</p>
          <input type="text" placeholder="姓名" />
        </div>
      </li>
      <li>
        <div>
          <p>详细地址</p>
          <input type="text" placeholder="街道门牌信息" v-model="address" />
        </div>
      </li>
      <li>
        <div>
          <p>邮政编码</p>
          <input type="text" placeholder="邮政编码" v-model="code" />
        </div>
      </li>
    </ul>
    <p class="ps" @click="save">保存</p>
    <p class="ps1">从微信读取</p>
    <p class="ps2">取消</p>
    <van-popup v-model="show" position="bottom">
      <van-area title="选择地区" :area-list="areaList" @confirm="ok" @cancel="quxiao" />
    </van-popup>
  </div>
</template>

<script>
import areaList from "../area";
import qs from "qs"
export default {
  data() {
    return {
      areaList: areaList,
      show: false,
      area: [],
      linkMan: "",
      mobile: "",
      address: "",
      code: ""
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    ads() {
      //   alert(1);
      this.show = true;
    },
    ok(data) {
      console.log(data);
      this.area = data;
      this.show = false;
    },
    quxiao() {
      this.show = false;
    },
    save() {
      var data = {
        address: this.address,
        cityId: this.area[1].code,
        code: this.code,
        linkMan: this.linkMan,
        mobile:this.mobile,
        provinceId: this.area[0].code,
        token: localStorage.getItem("token")
      };
      var d =qs.stringify(data)
      this.http
        .post("https://api.it120.cc/small4/user/shipping-address/add",d)
        .then(msg => {
            console.log(msg)
            // if(msg.data.msg=="success")
        });
    }
  }
};
</script>

<style lang="scss" scoped>
ul {
  width: 5.2rem;
  box-sizing: border-box;
  padding: 0.05rem 0.2rem 0 0.2rem;
  background: #ffffff;
  li {
    height: 0.71rem;
  
    div {
      border-bottom: 0.01rem solid #dddddd;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      height: 0.7rem;
      margin-left: 0.02rem;
        span {
      margin-right: 0.1rem;
      font-size: 0.2rem;
    }
      p {
        font-size: 0.2rem;
        width: 1.1rem;
      }
      input {
        font-size: 0.2rem;
        height: 0.2rem;
        width: 3.67rem;
        border: none;
        background: #ffffff;
        height: 0.64rem;
      }
    }
  }
}
.ps {
  width: 100%;
  background: #dddddd;
  text-align: center;
  line-height: 0.56rem;
  color: white;
  margin-top: 0.23rem;
  font-size: 0.22rem;
  letter-spacing: 0.02rem;
  border-radius: 0.06rem;
  height: 0.56rem;
  background: linear-gradient(to right, #81c125, #1aac19);
}
.ps1 {
  width: 100%;
  background: #dddddd;
  text-align: center;
  line-height: 0.56rem;
  color: white;
  margin-top: 0.23rem;
  font-size: 0.22rem;
  letter-spacing: 0.02rem;
  border-radius: 0.06rem;
  height: 0.56rem;
  background: linear-gradient(to right, #4bb0ff, #6149f6);
}
.ps2 {
  width: 100%;
  background: #dddddd;
  text-align: center;
  line-height: 0.56rem;
  color: white;
  margin-top: 0.23rem;
  font-size: 0.22rem;
  letter-spacing: 0.02rem;
  border-radius: 0.06rem;
  height: 0.56rem;
}
</style>


