<template>
  <div>
    <van-nav-bar title="确认付款" left-arrow @click-left="onClickLeft" />
    <div class="divs">
      <van-icon name="gold-coin-o" size="0.32rem" color="orange" />
      <div>
        <p>商品总额:<span style="color:red">￥{{data.amount}} </span> </p>
        <p>订单号:￥{{data.orderNumber}}</p>
      </div>
    </div>
    <div class="divs">
      <van-icon name="location-o" size="0.32rem" color="#1989fa" />
      <div>
        <p>
           <span>{{list.linkMan}}</span>
        <span>{{list.mobile}}</span>
        </p>
        <p>{{list.address}}</p>
      </div>
    </div>
    <p class="zi">微信支付 ￥0.01</p>
  </div>
</template>

<script>
import qs from "qs";
export default {
  data() {
    return {
      data: [],
      list:[]
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    }
  },
  created() {
    var re = this.$store.getters.order.map(item => {
      var obj = {};
      var str = [];
      obj.goodsId = item.id;
      obj.number = item.num;
      item.pro.forEach(element => {
        str.push(element.propertyId + ":" + element.id);
      });

      obj.propertyChildIds = str.join(",");
      return obj;
    });
    console.log(re);
    var token = localStorage.getItem("token");
    //   if (token != null) {
    //     this.$router.push("/pay");
    //   }else{
    //     this.$router.push("/login");
    //   }
    re = JSON.stringify(re);
    var d = {
      token: token,
      goodsJsonStr: re
    };
    var d1 = qs.stringify(d);
    this.http.post("https://api.it120.cc/small4/order/create", d1).then(msg => {
      console.log(msg);
      if (msg.data.msg == "success") {
        // console.log(234)
        this.data = msg.data.data;
        // this.$router.push({name:"pay",params:{ordernum:msg.data.data.orderNumber,price:msg.data.data.amount}})
      }
    });
  },
  mounted() {
    var data = { token: localStorage.getItem("token") };
    this.http
      .get(
        "https://api.it120.cc/small4/user/shipping-address/default?token=" +
          localStorage.getItem("token")
      )
      .then(msg => {
        console.log(msg);
        if (msg.data.msg == "success") {
          this.list = msg.data.data;
        }
      });
    
  }
};
</script>

<style lang="scss" scoped>
.divs {
  height: 1.17rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 0 0.2rem;
  border-top: 0.01rem solid #e9e9e9;
  p {
    margin-left: 0.25rem;
    height: 0.4rem;
    line-height: 0.4rem;
  }
   p:nth-of-type(2){
          color: #999;
    font-size: 0.14rem;
  }
}
.zi {
  width: 100%;
  height: 0.7rem;
  background: #b7282e;
  color: white;
  text-align: center;
  line-height: 0.7rem;
  font-size: 0.2rem;
  position: fixed;
  left: 0;
  bottom: 0;
 
}
</style>


