<template>
  <div>
    <div class="batgain">
      <p>人气推荐</p>
      <i class="van-icon van-icon-arrow"></i>
    </div>
    <van-loading v-if="loading" />
     <van-loading v-if="loading" />
    <ul v-else>
      <!-- <li v-for="item of list " :key="item.id" v-show="item.characteristic" >
        <img :src="item.pic" alt />
        <p>{{item.name}}</p>
        <p>{{item.characteristic}}</p>
        <p style="color:red">￥{{ item.originalPrice }}</p>
      </li> -->
        <Kjes   v-for="item of list " :key="item.id" :p="item" v-show="item.characteristic"></Kjes>
    </ul>
  </div>
</template>

<script>
import Kjes from "./Kjes"
export default {
  data() {
    return {
      list: [],
      loading:true
    };
  },
  created() {
    this.http.get("/shop/goods/list").then(msg => {
      console.log(msg);
      this.list = msg.data.data;
      this.loading=false
    });
  },
  components:{
      Kjes
  },

};
</script>

<style lang="scss" scoped>
.batgain {
  width: 100%;
  height: 0.8rem;
  line-height: 0.6rem;
  text-align: center;
  font-size: 0.2rem;
  display: flex;
  //   margin-bottom: 0.2rem;
  border-top: 0.2rem solid #f5f5f5;
  justify-content: center;
  i {
    line-height: 0.6rem;
    margin-left: 0.2rem;
  }
}
ul {
  width: 100%;
  padding: 0 0.18rem;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
    justify-content: space-between;

  li {
    width: 2.33rem;
    margin-right: 0rem;
    // display: flex;
    img {
      width: 2.33rem;
      height: 3rem;
      display: block;
      margin: 0 auto;
    }
    > p:nth-of-type(1) {
      font-size: 0.2rem;
      color: #000000;
      width: 2rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    > p:nth-of-type(2) {
      font-size: 0.14rem;
      color: #b2b2b2;
      width: 2rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      margin-top: 0.1rem;
    }
    > p:nth-of-type(3) {
      font-size: 0.16rem;
      color: #d00000;
      margin-top: 0.1rem;
    }
  }
}
</style>


