<template>
  <div>
    <van-nav-bar
      title="我的地址"
      right-text="新建地址"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />
    <ul>
      <li v-for="item of list" :key="item.id">
        <van-checkbox v-model="checked" checked-color="red" size="0.4rem"></van-checkbox>
        <div class="dd">
          <p>
            <span>{{item.linkMan}}</span>
            <span>{{item.mobile}}</span>
          </p>
          <p>
            <span>{{item.cityStr}}</span>
            -
            <span>{{item.address}}</span>
          </p>
        </div>
        <van-icon name="share" color="#ccc" size="0.4rem" />
      </li>
    </ul>
  </div>
</template>

<script>
import qs from "qs";
export default {
  data() {
    return {
      checked: true,
      list: []
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
      this.$router.push({ name: "address" });
    }
  },
  created() {
    var d = { token: localStorage.getItem("token") };
    var data = qs.stringify(d);
    this.http
      .post("https://api.it120.cc/small4/user/shipping-address/list", data)
      .then(msg => {
        console.log(msg);
        if (msg.data.msg == "success") {
          this.list = msg.data.data;
        }
      });
  }
};
</script>

<style lang="scss" scoped>
ul {
  //   padding-top: 0.72rem;
  background: #ffffff;
  width: 5.2rem;
  overflow: hidden;
  li {
    width: 6.23rem;
    height: 1.01rem;
    border-bottom: 1px solid #f5f5f5;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding-left: 0.38rem;
    background: #ffffff;
    position: relative;
    translate: all 0.5s;
    .dd {
      margin-left: 0.28rem;
      height: 0.58rem;
      width: 3.78rem;
      p:nth-of-type(1) {
        font-size: 0.2rem;
        margin-right: 0.12rem;
      }
      p:nth-of-type(2) {
        font-size: 0.16rem;
        margin-top: 0.21rem;
        color: #939393;
      }
    }
  }
}
</style>


