<template>
  <div>
    <div class="batgain">
      <p>精选专题</p>
      <i class="van-icon van-icon-arrow"></i>
    </div>
    <div class="isc">
      <van-loading v-if="loading" />
      <ul v-else>
        <!-- <li v-for="item in banner"  :key="item.id"  @click="gonews(item.id)">
          <img :src="item.pic" alt />
          <p>{{ item.title }}</p>
          <p>{{ item.descript }}</p>
        </li> -->
        <Zts v-for="item in banner" :z="item" :key="item.id" />
      </ul>
    </div>
  </div>
</template>

<script>
import Zts from "./Zts";
export default {
  data() {
    return {
      banner: [],
      loading: true
    };
  },
  created() {
    this.http.get("/cms/news/list").then(msg => {
      console.log(msg);
      this.banner = msg.data.data;
      this.loading = false;
    });
  },
  components: {
    Zts
  },
  // methods:{
  //   gonews(id){
  //     this.$router.push("/news?id="+id)
  //   }
  // }
};
</script>

<style lang="scss" scoped>
.batgain {
  width: 100%;
  height: 0.8rem;
  line-height: 0.6rem;
  text-align: center;
  font-size: 0.2rem;
  display: flex;
  //   margin-bottom: 0.2rem;
  border-top: 0.2rem solid #f5f5f5;
  justify-content: center;
  i {
    line-height: 0.6rem;
    margin-left: 0.2rem;
  }
}
.isc {
  width: 100%;
  height: 3.78rem;
  // overflow: hidden;
  overflow: auto;
  > ul {
    width: 22.7rem;
    display: flex;
    > li {
      width: 4.42rem;
      height: 2.84rem;
      margin-left: 0.12rem;
      > img {
        width: 4.42rem;
        height: 2.84rem;
        display: block;
        border-radius: 0.06rem;
      }
      > p:nth-of-type(1) {
        font-size: 0.18rem;
        color: #2c2c2c;
        margin-top: 0.12rem;
        width: 4rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      > p:nth-of-type(2) {
        font-size: 0.14rem;
        color: #a8a8a8;
        margin-top: 0.2rem;
        width: 3.24rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
  }
}
</style>


