<template>
  <li @click="goxq(p.id)">
    <img :src="p.pic" alt />
    <p>{{p.name}}</p>
    <p>{{p.characteristic}}</p>
    <p style="color:red">￥{{ p.originalPrice }}</p>
  </li>
</template>

<script>
export default {
  props: ["p"],
  methods: {
    goxq(id) {
      //  console.log(123)
      console.log(id);
     
      this.$router.push({ name: "xq", params: { id: id } });
    }
  }
};
</script>

<style lang="scss" scoped>
li {
  width: 2.33rem;
  margin-right: 0.1rem;
  img {
    width: 2.33rem;
    height: 3rem;
    display: block;
    margin: 0 auto;
  }
  > p:nth-of-type(1) {
    font-size: 0.2rem;
    color: #000000;
    width: 2rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  > p:nth-of-type(2) {
    font-size: 0.14rem;
    color: #b2b2b2;
    width: 2rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-top: 0.1rem;
  }
  > p:nth-of-type(3) {
    font-size: 0.16rem;
    color: #d00000;
    margin-top: 0.1rem;
  }
}
</style>


