<template>
  <li @click="goxq(d.id)">
    <div>
      <img :src="d.pic" alt class="pic" />
      <div>
        <p>{{ d.name}}</p>
        <p>{{ d.characteristic }}</p>
        <ol>
          <li>
            <p >￥{{d.minPrice}}</p>
            <p>低价</p>
          </li>
          <li>
            <p>￥{{d.originalPrice}}</p>
            <p>原价</p>
          </li>
          <li>
            <p>{{ d.stores }}件</p>
            <p>限量</p>
          </li>
        </ol>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  props: ["d"],
  data() {
    return {};
  },
  created() {},
  methods:{
   goxq(id){
     console.log(id)
     this.$router.push({name:"xq",params:{id}})
   }
  }
};
</script>

<style lang="scss" scoped >
</style>


