<template>
  <div class="flsp">
    <van-nav-bar
      title="分类商品"
      style=".van-icon:#000"
      :border="f"
      left-arrow
      @click-left="onClickLeft"
    />
    <ul v-if="list.length!=0">
      <li v-for="item of list" :key="item.id" @click="getxq(item.id)">
        <img :src="item.pic" alt />
        <p>{{item.name}}</p>
        <p>￥{{item.originalPrice}}</p>
      </li>
    </ul>
    <img v-else src="../assets/icon.png" alt class="pic" />
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      f: false
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    getxq(id){
        this.$router.push({name:"xq",params:{id:id}})
    }
  },
  created() {
    //   alert(this.$route.params.id)
    this.http.get("https://api.it120.cc/small4/shop/goods/list").then(msg => {
      console.log(msg);
      if (msg.data.msg == "success") {
        // console.log(123)
        let list = msg.data.data.filter(item => {
          return item.categoryId == this.$route.params.id;
        });
        console.log(list);
        this.list = list;
      }
    });
  }
};
</script>

<style lang="scss" scoped>
.flsp {
  .van-nav-bar {
    .van-icon {
      color: red;
    }
  }

  ul {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    li {
      width: 2.45rem;
      //   height: 2.45rem;
      position: relative;
      padding: 0 0.1rem;
      box-sizing: border-box;
      margin-right: 0.1rem;
      img {
        width: 2.45rem;
        height: 2.45rem;
        display: block;
        margin: 0 auto;
      }
      > p:nth-of-type(1) {
        width: 2.45rem;
        height: 0.4rem;
        background: blanchedalmond;
        position: absolute;
        bottom: 0.3rem;
        left: 0.1rem;
        color: #b69b72;
        font-size: 0.15rem;
        line-height: 0.4rem;
      }
      > p:nth-of-type(2) {
        color: red;
        font-size: 0.2rem;
      }
    }
  }
  .pic {
    width: 100%;
    height: 4.23rem;
  }
}
</style>


