<template>
  <div class="cart">
    <van-nav-bar title="购物车" :left-text="bian" @click-left="onClickLeft" />
    <div class="cc">
      <ul>
        <li v-for="(item,index) of list" :key="item.id">
          <van-checkbox v-model="item.checked" checked-color="red" @click="sel(index)"></van-checkbox>
          <img :src="item.pic" alt />
          <div class="ccs">
            <p>{{item.name}}</p>
            <p>
              <span v-for="(p,index1) of item.pro" :key="p.id">{{p.name}}</span>
            </p>
            <p class="p3">
              <span>￥{{item.price}}</span>
              <van-stepper v-model="item.num" @change="ch(index)" />
            </p>
          </div>
        </li>
      </ul>
    </div>
    <div class="place" v-show="list.length>0">
      <van-checkbox v-model="ss" checked-color="red" @change="qx" class="ccss">全选</van-checkbox>
      <p>合计：￥{{$store.getters.hj}}</p>
      <!-- <p>{{$store.getters.nums}}</p> -->
      <p @click="del">{{this.delete}}</p>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      checked: false,
      bian: "编辑",
      dd: false,
      delete: "下单"
    };
  },
  computed: {
    ...mapState({
      list: state => state.cart
    }),
    ss: {
      get: function() {
        return this.$store.getters.select;
      },
      set: function(v) {
        console.log(v);
        this.$store.commit("qx", v);
      }
    }
  },
  methods: {
    onClickLeft() {
      if (this.bian == "编辑") {
        this.bian = "完成";
        // this.checked = true;
        this.delete = "删除";
      } else {
        this.bian = "编辑";
        // this.checked = false;
        this.delete = "下单";
      }
    },
    sel(n) {
      this.list[n].checked;
      console.log(this.list[n].checked);
      this.$store.commit("sel", { index: n, value: this.list[n].checked });
    },
    ch(n) {
      // alert(this.list[n].num)
      this.$store.commit("num", { index: n, value: this.list[n].num });
    },
    qx(v) {
      console.log(v);
    },
    del() {
      if (this.delete == "删除") {
        this.$store.commit("del");
      } else {
        this.$router.push({ name: "order" });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.body {
  .van-nav-bar {
    .van-nav-bar {
      .van-nav-bar__text {
        color: red;
      }
    }
  }
}
.cart {
  width: 100%;

  .cc {
    ul {
      li {
        width: 5.2rem;
        display: flex;
        height: 1.6rem;
        display: -webkit-box;
        display: -ms-flexbox;

        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        padding-left: 0.16rem;
        box-sizing: border-box;

        position: relative;
        -webkit-transition: all 0.5s;
        transition: all 0.5s;
        img {
          width: 1.1rem;
          height: 1.1rem;
          display: block;
          border-radius: 0.04rem;
          margin-left: 0.17rem;
        }
        .ccs {
          width: 3.09rem;
          height: 1.1rem;
          margin-left: 0.24rem;
          align-items: center;
          > p:nth-of-type(1) {
            font-size: 0.18rem;
          }
          > p:nth-of-type(2) {
            display: flex;
            span {
              font-size: 0.14rem;
              margin-top: 0.16rem;
              color: #b2b2b2;
              display: block;
              margin-right: 0.08rem;
            }
          }
          .p3 {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-align: center;
            -ms-flex-align: center;
            align-items: center;
            margin-top: 0.17rem;
            width: 3.09rem;
            -webkit-box-pack: justify;
            -ms-flex-pack: justify;
            justify-content: space-between;
            span {
              color: red;
              font-size: 0.18rem;
            }
          }
        }
      }
    }
  }
  .place {
    height: 0.68rem;
    border-top: 0.01rem solid #e9e9e9;
    border-bottom: 0.01rem solid #e9e9e9;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    background: white;
    z-index: 1;
    box-sizing: border-box;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding-left: 0.16rem;
    width: 100%;
    position: fixed;
    left: 0;
    bottom: 0.87rem;
    .ccss {
      width: 2.1rem;
    }
    > p:nth-of-type(1) {
      font-size: 0.18rem;
      color: #d00000;
    }
    > p:nth-of-type(2) {
      width: 1.66rem;
      height: 0.68rem;
      font-size: 0.2rem;
      color: white;
      text-align: center;
      line-height: 0.68rem;
      background: #d00000;
    }
  }
}
</style>


