<template>
  <div class="cate">
    <p>
      <input placeholder="回车搜索商品" type="text" />
    </p>
    <div>
      <ol>
        <li @click="all">所有分类</li>
        <li v-for="item of fen1" :key="item.id" @click="getChildren(item.id)">{{item.name}}</li>
      </ol>
      <div class="psd">
        <div class="dd">
          <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
            <van-swipe-item>
              <img src="../assets/1.jpg" alt />
            </van-swipe-item>
            <van-swipe-item>
              <img src="../assets/2.jpg" alt />
            </van-swipe-item>
            <van-swipe-item>
              <img src="../assets/3.jpg" alt />
            </van-swipe-item>
            <van-swipe-item>
              <img src="../assets/4.jpg" alt />
            </van-swipe-item>
          </van-swipe>
        </div>
        <ul class="mean">
          <li v-for="item of fen2" :key="item.id" @click="getflsp(item.id)">
            <img :src="item.icon" alt />
            <p>{{item.name}}</p>
          </li>
          <!-- <li v-for="item of fen2" :key="item.id">
            <img :src="item.icon" alt />
            <p>{{item.name}}</p>
          </li>-->
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      fen: [],
      fen1: [],
      fen2: []
    };
  },
  methods: {
    async getData() {
      //获取分类数据的方法
      let d = await this.http.post(
        "https://api.it120.cc/small4/shop/goods/category/all"
      );
      console.log(d);

      if (d.data.msg == "success") {
        var fen1 = d.data.data.filter(item => {
          return item.level == 1;
        });
        console.log(fen1);
        this.fen1 = fen1;
      }

      this.fen = d.data.data;
      this.fen2 = d.data.data;
    },
    getChildren(id) {
      // console.log(id)
      let d2 = this.fen.filter(item => {
        return item.pid == id;
      });
      console.log(d2);
      this.fen2 = d2;
    },
    all() {
      this.fen2 = this.fen;
    },
    getflsp(id) {
      this.$router.push({ name: "fenlsp", params: { id: id } });

      // this.$router.push("/fenlsp")
    }
  },
  created() {
    this.getData();
  }
};
</script>

<style lang="scss" scoped>
.cate {
  > p:nth-of-type(1) {
    width: 100%;
    height: 0.8rem;
    // border-bottom: 0.01rem solid #f3f3f3;
    display: flex;
    align-items: center;
    input {
      width: 4rem;
      height: 0.5rem;
      background: #f5f5f5;
      border: none;
      border-radius: 0.3rem;
      margin-left: 0.2rem;
      padding-left: 0.2rem;
      font-size: 0.03rem;
      box-sizing: border-box;
    }
  }
  div {
    display: flex;
    width: 100%;

    ol {
      border-top: 0.01rem solid #f3f3f3;
      width: 1.27rem;
      border-right: 0.01rem solid #e9e9e9;
      // height: 5.3rem;
      height: 100%;
      position: fixed;
      left: 0;
      top: 0.81rem;
      overflow: auto;
      background: white;
      z-index: 1;
      li {
        width: 1.27rem;
        height: 0.5rem;
        // border-left: 0.03rem solid white;//#D41515
        font-size: 0.2rem;
        // color: #675D56;
        text-align: center;
        line-height: 0.5rem;
        margin-top: 0.1rem;
      }
      li:hover {
        border-left: 0.02rem solid red;
        color: red;
      }
    }
    .psd {
      border-top: 0.01rem solid #f3f3f3;
      position: fixed;
      left: 1.27rem;
      width: 3.92rem;
      // top: 0.81rem;
      overflow: auto;
      height: 100%;
      display: flex;
      flex-wrap: wrap;
      .dd {
        width: 3.6rem;
        height: 2rem;
        // background: red;
        margin: 0 auto;
        margin-top: 0.2rem;
        .my-swipe .van-swipe-item {
          img {
            width: 3.6rem;
            height: 2rem;
          }
        }
      }

      .mean {
        width: 100%;
        display: flex;      
        flex-wrap: wrap;
        li {
          width: 30%;
          margin-left: 0.1rem;
          img {
            width: 1.1rem;
            height: 1.1rem;
            display: block;
            margin: 0 auto;
            margin-top: 0.1rem;
          }
          p {
            display: block;
            font-size: 0.16rem;
            text-align: center;
            margin-top: 0.1rem;
          }
        }
      }
    }
  }
}
</style>


