<template>
  <div class="xq">
    <div class="fan" @click="gos">
      <p>
        <van-icon name="arrow-left" color="red" size="0.36rem" />
      </p>
    </div>
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="item of swiper" :key="item.id">
        <img :src="item.pic" alt />
      </van-swipe-item>
    </van-swipe>
    <div class="ku">
      <p>{{Info.name}}</p>
      <p>{{Info.characteristic}}</p>
      <div>
        <p>
          低价
          <span>￥{{Info.minPrice}}</span>
        </p>
        <p>原价{{Info.originalPrice}}</p>
        <p>库存{{Info.stores}}</p>
      </div>
    </div>
    <!-- <ul class="xuan">
      <li>商品介绍</li>
      <li>商品评价</li>
    </ul>
    <div v-html="content" class="cons" style="width:5.2rem"></div>-->
    <van-tabs @click="onClick" @change="ch">
      <van-tab title="商品介绍" v-html="content" class="cons"></van-tab>
      <van-tab title="商品评价">
        <ul>
          <li v-for="item of ping" :key="item.id" class="pings">
            <van-icon name="https://b.yzcdn.cn/vant/icon-demo-1126.png" size="1rem" />
            <div>
              <p>
                {{item.user.nick==(""||undefined)?'匿名用户':item.user.nick}}
                <span>{{item.goods.goodReputationStr}}</span>
              </p>
              <p>{{item.goods.goodReputationRemark}}</p>
              <div>
                <p>{{item.user.dateLogin}}</p>
                <p>选择规格:{{item.goods.property}}</p>
              </div>
            </div>
          </li>
        </ul>
      </van-tab>
    </van-tabs>
    <van-popup v-model="show" closeable position="bottom" class="mianban">
      <div>
        <img :src="Info.pic" alt />
        <div>
          <p>{{Info.name}}</p>
          <p>￥{{Info.minPrice}}</p>
        </div>
      </div>

      <div v-for="(p,index1) of ps " :key="p.id" class="menu">
        <p>{{p.name}}</p>
        <div
          class="redwu"
          :class="{active:p2.checked}"
          @click="select(index1,index2)"
          v-for="(p2,index2) of p.childsCurGoods"
          :key="p2.id"
        >{{p2.name}}</div>
      </div>

      <div class="num">
        <p>购买数量</p>
        <van-stepper v-model="value" />
      </div>
      <p class="ss" @click="buy">加入购物车</p>
    </van-popup>
    <van-goods-action>
      <van-goods-action-icon icon="service-o" />
      <van-goods-action-icon icon="shopping-cart-o" :badge="$store.getters.nums" @click="goCart" />
      <van-goods-action-icon icon="star-o" />
      <van-goods-action-button type="warning" text="立即购买" />
      <van-goods-action-button type="danger" text="加入购物车" @click="getcart" />
    </van-goods-action>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      swiper: [],
      Info: [],
      content: "",
      ping: [],
      show: false,
      value: 1,
      ps: []
    };
  },
  created() {
    // alert(this.$route.params.id);
    this.http
      .post(
        "https://api.it120.cc/small4/shop/goods/detail?id=" +
          this.$route.params.id
      )
      .then(msg => {
        console.log(msg);
        // if(msg.data.msg=="success"){
        //     console.log(234)
        // }
        this.swiper = msg.data.data.pics;
        this.Info = msg.data.data.basicInfo;
        this.content = msg.data.data.content;
        console.log(msg.data.data.properties);

        var arr = msg.data.data.properties;
        arr.forEach(item => {
          item.childsCurGoods.forEach(a => {
            a.checked = false;
          });
        });
        this.ps = arr;
        console.log(this.ps);
      });
  },
  methods: {
    goCart() {
      this.$router.push("/home/cart");
    },
    gos() {
      this.$router.go(-1);
    },
    onClick(name, title) {
      Toast(title);
    },
    ch(e) {
      // console.log(e)
      if (e == 1) {
        this.http
          .post(
            "https://api.it120.cc/small4/shop/goods/reputation?goodsId=" +
              this.$route.params.id
          )
          .then(msg => {
            // console.log(msg);
            if (msg.data.msg == "success") {
              this.ping = msg.data.data;
              // console.log(msg.data.data);
            } else {
              console.log("数据请求失败");
            }
          });
      }
    },
    getcart() {
      this.show = true;
    },
    select(a, b) {
      // console.log(a, b);
      this.ps[a].childsCurGoods.forEach((item, index) => {
        if (index == b) {
          item.checked = true;
          this.$set(this.ps[a].childsCurGoods, index, item);
        } else {
          item.checked = false;
          this.$set(this.ps[a].childsCurGoods, index, item);
        }
      });
    },
    buy() {
      // alert(111)
      let t = localStorage.getItem("token");
      console.log(t);
      if (t) {
        // alert("已经登录了");
        //把数据添加到仓库
        var p0 = [];
        this.ps.forEach(item => {
          var r = item.childsCurGoods.filter(p => {
            return p.checked;
          });
          p0 = p0.concat(r);
        });
        console.log(p0);
        if (p0.length < this.ps.length) {
          this.$notify("请选择商品规格");
          return false;
        }
        var obj = {
          pic: this.Info.pic,
          name: this.Info.name,
          price: this.Info.minPrice,
          num: this.value,
          pro: p0,
          checked: false,
          id: this.Info.id
        };
        this.$store.commit("addCart", obj);
        Toast.success("添加购物车成功");
        this.show = false;
      } else {
        this.$router.push("/login");
        //  alert("请输入账号密码")
        Toast.fail("请输入账号密码");
        return false;
      }
    }
  }
};
</script>

<style lang="scss">
.xq {
  .fan {
    width: 0.46rem;
    height: 0.46rem;
    padding: 0.03rem;
    box-sizing: border-box;
    border-radius: 50%;
    border: 0.02rem solid #ffffff;
    position: fixed;
    left: 0.1rem;
    top: 0.1rem;
    text-align: center;
    line-height: 0.36rem;
    z-index: 123;
    display: flex;
    p {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background: white;
      text-align: center;
      display: flex;
      margin-top: -0rem;
      .van-icon {
        opacity: 0.1;
      }
    }
  }
  .my-swipe .van-swipe-item {
    img {
      width: 5.2rem;
      height: 5.2rem;
    }
  }
  .ku {
    width: 5rem;
    height: 1.24rem;
    background: #ffffff;
    padding-left: 0.2rem;
    border-bottom: 0.14rem solid #f5f5f5;
    > p:nth-of-type(1) {
      font-size: 0.22rem;
      color: #464646;
      margin-top: 0.3rem;
    }
    > p:nth-of-type(2) {
      font-size: 0.16rem;
      color: #b8b8b8;
      margin-top: 0.16rem;
    }
    > div {
      font-size: 0.16rem;
      display: flex;
      margin-top: 0.2rem;
      > p:nth-of-type(1) {
        font-size: 0.14rem;
        color: #cc0c0c;
        width: 0.9rem;
        display: flex;
        margin-top: 0.06rem;

        span {
          font-size: 0.2rem;
          display: block;
          margin-top: -0.03rem;
        }
      }
      > p:nth-of-type(2) {
        font-size: 0.14rem;
        color: #b8b8b8;
        margin-top: 0.06rem;
        width: 3.2rem;
      }
      > p:nth-of-type(3) {
        font-size: 0.14rem;
        color: #b8b8b8;
        margin-top: 0.06rem;
      }
    }
  }
  .xuan {
    width: 100%;
    height: 0.6rem;
    display: flex;
    li {
      width: 50%;
      height: 0.57rem;
      text-align: center;
      line-height: 0.57rem;
      font-size: 0.22rem;
    }
    li:hover {
      border-bottom: 0.03rem solid #ff0000;
    }
  }
  .cons {
    margin-top: 0.2rem;
    width: 5.2rem;
    overflow: auto;
    .img-lazyloaded {
      width: 100% !important;
      height: 1.3rem !important;
    }
    ul {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      li {
        font-size: 0.2rem;
        width: 100%;
        height: 0.47rem;
        line-height: 0.27rem;
        padding-left: 0.14rem;
        padding-right: 0.24rem;
        box-sizing: border-box;
        display: flex;
        flex-wrap: wrap;
        :nth-last-of-type() {
          height: 1rem;
          p {
            display: flex;
            flex-wrap: wrap;
            span {
              display: block;
              display: flex;
              flex-wrap: wrap;
            }
          }
        }
      }
    }
  }
  .pings {
    display: flex;
    border-bottom: 1px solid #ccc;
    padding: 0.1rem 0.1rem;
    box-sizing: border-box;
    div {
      width: 3.5rem;
      height: 1rem;
      margin-bottom: 0.1rem;
      margin-left: 0.2rem;
      p {
        margin-bottom: 0.06rem;
        font-size: 0.16rem;
      }
      > p:nth-of-type(1) {
        font-size: 0.16rem;
        span {
          // display: block;
          background: red;
          padding: 0.06rem;
          color: #fff;
        }
      }
      > p:nth-of-type(2) {
        font-size: 0.16rem;
        height: 0.2rem;
      }
      > div {
        display: flex;
        margin-left: -0rem;
        p {
          text-align: left;
          font-size: 0.16rem;
        }
        p:nth-of-type(1) {
          width: 1.2rem;
          padding-left: -0.2rem;
        }
      }
    }
  }
  .mianban {
    width: 100%;
    > div:nth-of-type(1) {
      margin-bottom: 0.2rem;
      height: 1.23rem;
      border-bottom: 0.01rem solid #e9e9e9;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      img {
        width: 0.85rem;
        height: 0.85rem;
        display: block;
        margin-left: 0.2rem;
        margin-top: 0.2rem;
        background: #000;
      }
      div {
        margin-left: 0.16rem;
        width: 3.5rem;
        > p:nth-of-type(1) {
          font-size: 0.17rem;
          margin-top: 0.3rem;
        }
        > p:nth-of-type(2) {
          font-size: 0.18rem;
          color: #d00000;
          margin-top: 0.24rem;
        }
      }
    }
    > .menu {
      // padding: 0.3rem 0;
      margin-bottom: 0.2rem;
      display: flex;
      flex-wrap: wrap;
      p {
        font-size: 0.16rem;
        margin-left: 0.2rem;
        width: 100%;
      }
      div {
        padding: 0.1rem 0.2rem;
        border-radius: 0.06rem;
        margin-top: 0.1rem;
        margin-left: 0.1rem;
      }
      .redwu {
        border: 0.01rem solid #d9d9d9;
      }
      .active {
        border-color: red;
      }
    }
    > .num {
      border-top: 0.01rem solid #e9e9e9;
      height: 0.98rem;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
      -ms-flex-pack: justify;
      justify-content: space-between;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      padding: 0 0.2rem;
    }
    .ss {
      width: 100%;
      height: 0.6rem;
      text-align: center;
      line-height: 0.6rem;
      font-size: 0.22rem;
      color: white;
      background: #b7282e;
    }
  }
}
</style>


