<template>
  <div>
     <van-loading v-if="loading" />
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white" v-else>
      <van-swipe-item v-for="item of banner " :key="item.id">
        <img :src="item.picUrl" alt />
      </van-swipe-item>
    </van-swipe>
    <ul>
      <li>
        <img src="../assets/2.png" alt />
        <p>签到</p>
      </li>
      <li>
        <img src="../assets/3.png" alt />
        <p>礼券</p>
      </li>
      <li>
        <img src="../assets/4.png" alt />
        <p>砍价</p>
      </li>
      <li>
        <img src="../assets/1.png" alt />
        <p>专栏</p>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      banner: [],
      loading:true
    };
  },
  created() {
    this.http.get("https://api.it120.cc/small4/banner/list").then(msg => {
      console.log(msg);
      this.banner = msg.data.data;
      this.loading=false
    });
  }
};
</script>

<style lang="scss" scoped>
.my-swipe .van-swipe-item {
  img {
    width: 100%;
    display: block;
    position: relative;
  }
}
ul {
  display: flex;
  background: #ffffff;
  width: 100%;
  height: 1.6rem;
  padding: 0.2rem 0rem;
  box-sizing: border-box;
  border-radius: 20% 20% 0 0;
  border-bottom: 0.2rem solid #f5f5f5;
  position: absolute;
  top: 4rem;
  left: 0rem;
  li {
    width: 25%;
    img {
      margin: auto;
      width: 0.58rem;
      height: 0.58rem;
      display: block;
    }
    p {
      font-size: 0.26rem;
      display: block;
      text-align: center;
      margin-top: 0.2rem;
    }
  }
}
</style>


