import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from "@/pages/Home"
import Index from "@/pages/Index"
import Cate from "@/pages/Cate"
import Cart from "@/pages/Cart"
import User from "@/pages/User"
import News from "@/pages/News"
import Login from "@/pages/Login"
import Zuce from "@/pages/Zuce"
import FenLSP from "@/pages/FenLSP"
import XQ from "@/pages/XQ"
import Order from "@/pages/Order"
import Addresslist from "@/pages/Addresslist"
import Address from "@/pages/Address"
import Pay from "@/pages/Pay"
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      redirect: "/home/index"
    },
    {
      path: "/home",
      name: "home",
      component: Home,

      children: [
        {
          path: "index",
          component: Index
        },
        {
          path: "cate",
          component: Cate
        },
        {
          path: "cart",
          component: Cart
        },
        {
          path: "user",
          component: User
        }
      ]
    },
    {
      path: '/news',
      component: News,
      name: "news1"
    },
    {
      path: '/news/:id',
      component: News,
      name: "news2"
    },
    {
      path: "/login",
      component: Login,
      name: "login"
    }, {
      path: "/zuce",
      component: Zuce,
      name: "zuce"
    },{
      path:"/fenlsp/:id",
      component:FenLSP,
      name:"fenlsp"
    },{
      path:'/xq/:id',
      component:XQ,
      name:"xq"
    },
    {
      path:"/order",
      component:Order,
      name:"order"
    },
    {
      path:"/addresslist",
      component:Addresslist,
      name:"addresslist"
    },{
      path:"/address",
      component:Address,
      name:"address"
    },{
      path:"/pay",
      component:Pay,
      name:"pay"
    }
  ]
})



