<template>
  <li @click="gonews(z.id)">
    <img :src="z.pic" alt />
    <p>{{ z.title }}</p>
    <p>{{ z.descript }}</p>
    <!-- <p>{{z.id}}</p> -->
  </li>
</template>

<script>
export default {
  props: ["z"],
  methods: {
    gonews(id) {
      // this.$router.push("/news?id" + id);
      // this.$router.push('/news/'+id)
      // this.$router.push({name:"news2",params:{id:id}})
      this.$router.push({ name: "news1", query: { id: id } });
    }
  },
  
};
</script>

<style lang="scss"  scoped>
li {
  width: 4.42rem;
  height: 2.84rem;
  margin-left: 0.12rem;
  > img {
    width: 4.42rem;
    height: 2.84rem;
    display: block;
    border-radius: 0.06rem;
  }
  > p:nth-of-type(1) {
    font-size: 0.18rem;
    color: #2c2c2c;
    margin-top: 0.12rem;
    width: 4rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  > p:nth-of-type(2) {
    font-size: 0.14rem;
    color: #a8a8a8;
    margin-top: 0.2rem;
    width: 3.24rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
</style>


