<template>
  <div class="login">
    <div>
      <p>登陆账号</p>
      <p>虾米 — 严选商城</p>
      <p>
        <input autofocus="autofocus" type="tel" placeholder="手机号码" v-model="tel" />
        <span style="display:none">必填</span>
        <van-icon name="orders-o" size="0.4rem" />
      </p>
      <p>
        <input type="password" placeholder="密码" v-model="pass" v-if="eye" />
        <input type="text" placeholder="密码" v-model="pass" v-else />
        <van-icon name="eye-o" size="0.4rem" @click="eyes" v-if="ewe" />
        <van-icon name="closed-eye" size="0.4rem" @click="eyess" v-else />
      </p>
      <p @click="logins">立即登陆</p>
      <p>忘记密码</p>
      <p @click="gozuce">还没有密码？立即注册</p>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
import qs from "qs";
export default {
  data() {
    return {
      pass: "",
      tel: "",
      eye: true,
      ewe: true,
      p1: ""
    };
  },
  methods: {
    eyes() {
      this.eye = false;
      this.ewe = false;
    },
    eyess() {
      this.eye = true;
      this.ewe = true;
    },
    logins() {
      let data = { mobile: this.tel, pwd: this.pass };
      this.http
        .post(
          "https://api.it120.cc/small4/user/m/login?deviceId=007&deviceName=monkey",
          qs.stringify(data)
        )
        .then(msg => {
          console.log(msg);
          if (msg.data.msg == "success") {
            // alert("登陆成功");

            localStorage.setItem("token", msg.data.data.token);

            if (this.p1 != "/") {
              this.$router.push(this.p1);
            } else {
              this.$router.push("/home/user");
            }
            Toast.success("登陆成功");
          } else {
            // alert("登陆失败,用户名或密码不对");
            Toast.fail("请正确输入用户名或密码");
          }
        });
    },
    gozuce() {
      this.$router.push("/zuce");
    }
  },
  created() {
    console.log(this.$router);
  },
  beforeRouteEnter(to, from, next) {
    // ...
    console.log(from);
    next(vm => {
      vm.p1 = from.path;
    });
  }
};
</script>

<style lang="scss" scoped>
.login {
  padding: 0.26rem;
  min-height: 2rem;
  box-sizing: border-box;
  div {
    width: 4.6rem;
    padding: 0 0.44rem;
    box-sizing: border-box;
    min-height: 10rem;
    border: 0.02rem solid #f5f5f5;
    > p:nth-of-type(1) {
      font-size: 0.3rem;
      color: #666;
      margin-top: 0.44rem;
    }
    > p:nth-of-type(2) {
      font-size: 0.2rem;
      color: #909090;
      margin-top: 0.36rem;
    }
    > p:nth-of-type(3) {
      width: 3.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
      > input {
        margin-left: 0.2rem;
        width: 2.6rem;
        height: 0.4rem;
        border: none;
        outline: none;
        background: #f5f5f5;
        font-size: 0.2rem;
        color: #bfbfbf;
      }
      > span {
        font-size: 0.18rem;
        color: #e12c1e;
      }
      > i {
        font-size: 0.22rem;
        margin-left: 0.3rem;
      }
    }
    > p:nth-of-type(4) {
      width: 3.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
      input {
        margin-left: 0.2rem;
        width: 2.6rem;
        height: 0.4rem;
        border: none;
        outline: none;
        background: #f5f5f5;
        font-size: 0.2rem;
        color: #bfbfbf;
      }
      > i {
        font-size: 0.22rem;
        margin-left: 0.3rem;
      }
    }
    > p:nth-of-type(5) {
      width: 2.91rem;
      height: 0.57rem;
      border-radius: 0.06rem;
      background: linear-gradient(90deg, #5ea6f8, #bb87f6);
      text-align: center;
      line-height: 0.57rem;
      font-size: 0.2rem;
      color: white;
      letter-spacing: 0.06rem;
      margin: 0 auto;
      margin-top: 0.24rem;
    }
    > p:nth-of-type(6) {
      font-size: 0.16rem;
      color: #989898;
      width: 100%;
      text-align: center;
      margin-top: 0.36rem;
    }
    > p:nth-of-type(7) {
      font-size: 0.2rem;
      color: #549ff9;
      margin-top: 0.27rem;
      text-align: center;
    }
  }
}
</style>


