<template>
  <div class="login">
    <div>
      <p>新注册用户</p>
      <p>很高兴您将成为商城的会员（注册只需一步）</p>
      <p>
        <input autofocus="autofocus" type="tel" placeholder="手机号码" v-model="tel" />
        <span style="display:none">必填</span>
        <van-icon name="orders-o" size="0.4rem" />
      </p>
      <p>
        <input type="password" placeholder="密码" v-model="pass" v-if="on" />
        <input type="text" placeholder="密码" v-model="pass" v-else />
        <van-icon name="eye-o" size="0.4rem" v-if="eye" @click="eyes" />
        <van-icon name="closed-eye" size="0.4rem" v-else @click="eyess" />
      </p>
      <p>
        <input type="password" placeholder="确认密码" v-if="ons" />
        <input type="text" placeholder="确认密码" v-else />
        <van-icon name="eye-o" size="0.4rem" v-if="close" @click="colses" />
        <van-icon name="closed-eye" size="0.4rem" v-else @click="colsess" />
      </p>
      <p>
        <input type="text" placeholder="用户名" />
        <van-icon name="user-o" size="0.4rem" />
      </p>
      <p>
        <input type="text" v-model="pic" />
        <img :src="imgcode +key " @click="updateImg" />
      </p>
      <p></p>
      <p>
        <input type="text" v-model="code" />
        <span @click="getcode" v-if="show">获取验证码</span>
        <span v-else>{{count}}秒后再试</span>
      </p>
      <p @click="zuce">立即注册</p>
      <p @click="gologin" class>已有账号？ 立即登录</p>
    </div>
    <!-- <Dao></Dao> -->
  </div>
</template>

<script>
import Dao from "../components/Dao";
import { v4 as uuidv4 } from "uuid";
import qs from "qs";
export default {
  data() {
    return {
      imgcode: "https://api.it120.cc/small4/verification/pic/get?key=",
      eye: true,
      on: true,
      close: true,
      ons: true,
      tel: "",
      pass: "",
      pic: "",
      key: uuidv4(),
      code: "",
      show: true,
      count: "",
      timer: null
    };
  },
  methods: {
    updateImg() {
      this.key = uuidv4();
    },
    getcode() {
      // alert(this.tel)
      var data = { mobile: this.tel, picCode: this.pic, key: this.key };
      this.http
        .post(
          "https://api.it120.cc/small4/verification/sms/get",
          qs.stringify(data)
        )
        .then(msg => {
          console.log(msg);
          if (msg.data.msg == "success") {
            alert("验证码发送成功请等待");
          } else {
            alert("验证码发送失败，请重新填写");
          }
        });
      const TIME_COUNT = 60;
      if (!this.timer) {
        this.count = TIME_COUNT;
        this.show = false;
        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= TIME_COUNT) {
            this.count--;
          } else {
            this.show = true;
            clearInterval(this.timer);
            this.timer = null;
          }
        }, 1000);
      }
    },
    zuce() {
      var data = { mobile: this.tel, pwd: this.pass, code: this.code };
      this.http
        .post("https://api.it120.cc/small4/user/m/register", qs.stringify(data))
        .then(msg => {
          console.log(msg);
          if (msg.data.msg == "success") {
            alert("注册成功");
          } else if (msg.data.msg == "user has exists") {
            alert("您已经注册过了");
          } else {
            alert("注册失败");
          }
        });
    },
    gologin() {
      this.$router.push("/login");
    },
    eyes() {
      this.eye = false;
      this.on = false;
    },
    eyess() {
      this.eye = true;
      this.on = true;
    },
    colses() {
      this.close = false;
      this.ons = false;
    },
    colsess() {
      this.close = true;
      this.ons = true;
    }
    
  },
  components: {
    Dao
  }
};
</script>

<style lang="scss" scoped>
.login {
  padding: 0.26rem;
  min-height: 2rem;
  box-sizing: border-box;
  div {
    width: 4.6rem;
    padding: 0 0.44rem;
    box-sizing: border-box;
    min-height: 10rem;
    border: 0.02rem solid #f5f5f5;
    > p:nth-of-type(1) {
      font-size: 0.3rem;
      margin-top: 0.5rem;
    }
    > p:nth-of-type(2) {
      font-size: 0.2rem;
      color: #909090;
      margin-top: 0.5rem;
      margin-bottom: 0.23rem;
    }
    > p:nth-of-type(3) {
      width: 3.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
      > input {
        margin-left: 0.2rem;
        width: 2.6rem;
        height: 0.4rem;
        border: none;
        outline: none;
        background: #f5f5f5;
        font-size: 0.2rem;
        color: #bfbfbf;
      }
      > span {
        font-size: 0.18rem;
        color: #e12c1e;
      }
      > i {
        font-size: 0.22rem;
        margin-left: 0.3rem;
      }
    }
    > p:nth-of-type(4),
    > p:nth-of-type(5),
    > p:nth-of-type(6) {
      width: 3.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
      input {
        margin-left: 0.2rem;
        width: 2.6rem;
        height: 0.4rem;
        border: none;
        outline: none;
        background: #f5f5f5;
        font-size: 0.2rem;
        color: #bfbfbf;
      }
      > i {
        font-size: 0.22rem;
        margin-left: 0.3rem;
      }
    }

    > p:nth-of-type(7) {
      width: 3.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: flex;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5; //e12c1e
      input {
        margin-left: 0.2rem;
        width: 1.5rem;
        height: 0.4rem;
        border: none;
        outline: none;
        background: #f5f5f5;
        font-size: 0.2rem;
      }
      img {
        width: 2rem;
        height: 0.65rem;
        position: absolute;
        right: 0;
        top: 0;
      }
    }
    > p:nth-of-type(8) {
      width: 3.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: flex;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5;
      font-size: 0.2rem;
      span {
        margin-left: 0.2rem;
      }
    }
    > p:nth-of-type(9) {
      width: 3.72rem;
      height: 0.66rem;
      border-radius: 0.08rem;
      background: #f5f5f5;
      margin-top: 0.24rem;
      display: flex;
      align-items: center;
      position: relative;
      border: 1px solid #f5f5f5; //e12c1e
      input {
        margin-left: 0.2rem;
        width: 1.7rem;
        height: 0.4rem;
        border: none;
        outline: none;
        background: #f5f5f5;
        font-size: 0.2rem;
      }
      span {
        display: block;
        width: 1.7rem;
        height: 0.65rem;
        border-left: 0.01rem solid #e1e1e1;
        font-size: 0.2rem;
        text-align: center;
        line-height: 0.65rem;
        cursor: pointer;
      }
    }
    > p:nth-of-type(10) {
      width: 2.91rem;
      height: 0.57rem;
      border-radius: 0.06rem;
      background: linear-gradient(90deg, #5ea6f8, #bb87f6);
      text-align: center;
      line-height: 0.57rem;
      font-size: 0.2rem;
      color: white;
      letter-spacing: 0.06rem;
      margin: 0 auto;
      margin-top: 0.24rem;
    }
    > p:nth-of-type(11) {
      font-size: 0.2rem;
      color: #549ff9;
      margin-top: 0.27rem;
      text-align: center;
    }
  }
}
</style>


