<template>
  <div class="kanjia">
    <div class="batgain">
      <p>全民砍价</p>
      <i class="van-icon van-icon-arrow"></i>
    </div>
    <van-loading v-if="loading" />
    <ul v-else>
      <Kjs v-for="item of mean" :key="item.id" :d="item" v-show="item.characteristic"  @click="goxq(item.id)" />
    </ul>
  </div>
</template>

<script>
import Kjs from "./Kjs";
export default {
  data() {
    return {
      mean: [],
      loading: true
    };
  },
  created() {
    this.http.get("/shop/goods/kanjia/list").then(msg => {
      console.log(msg);
      this.mean = msg.data.data.goodsMap;
      this.loading = false;
    });
  },
  components: {
    Kjs
  },
  methods:{
    goxq(e){
     alert(e)
   }
  }
};
</script>

<style lang="scss">
.kanjia {
  .batgain {
    width: 100%;
    height: 0.6rem;
    line-height: 0.6rem;
    text-align: center;
    font-size: 0.2rem;
    display: flex;
    margin-top: -0rem;
    margin-top: 0.5rem;
    justify-content: center;
    i {
      line-height: 0.6rem;
      margin-left: 0.2rem;
    }
  }
  ul {
    li {
      > div {
        width: 100%;
        display: flex;
        padding: 0.1rem;
        box-sizing: border-box;
        border-top: 0.01rem solid #e9e9e9;
        > .pic {
          width: 1.58rem;
          height: 1.58rem;
          margin-right: 0.2rem;
        }
        > div {
          > p:nth-of-type(1) {
            font-size: 0.2rem;
            width: 2.5rem;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            text-align: left;
  
          }
          > p:nth-of-type(2) {
            width: 2.5rem;
            font-size: 0.15rem;
            color: #b2b2b2;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            margin-top: 0.1rem;
            text-align: left;
          }
          > ol {
            width: 2.3rem;
            height: 1rem;
            display: flex;
            margin-left: -0.1rem;
            > li {
              width: 33%;
              font-size: 0.2rem;
              color: #b2b2b2;
              margin-top: 0.25rem;
              p {
                display: block;
                text-align: center;
                margin-top: 0.1rem;
              }
            }
            > li:nth-of-type(1) {
              font-size: 0.2rem;
              text-align: center;
              display: block;

              color: red;
              p:nth-of-type(2) {
                color: black;
              }
            }
          }
        }
      }
    }
  }
}
</style>


